/* Page Styles */
body{
  padding: 0;
  margin: 0;
  width: 100vh;
  max-width: 100%;
  height: 300vh;
  background-color: black;
  /* background-image: url("./images/BG.png");
  background-size: cover; */
  color: white;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
a.brand:link,
a.brand:visited {
  color: #e51b88;
}
a.brand:hover {
  color: white;
  font-size: 1.1em;
}
a.nav:link,
a.nav:visited {
  color: white;
}
a.nav:hover {
  color: #e51b88;
  font-size: 1.4em;
}
.outline-font {
  font-family: "Londrina Outline", cursive;
}
.thin-font {
  font-family: "Londrina Solid", cursive;
}
.bg {
  width: 100vw;
  z-index: index -999;
  position: absolute;
}
/* Nav Bar Styles */
.header {
  width: 90vw;
  height: auto;
  padding: 5px;
  /* padding-left: 5vw; */
  padding-right: 5vw;
  position: fixed;
  font-size: xx-large;
  /* background-color: black; */
  display: inline-grid;
  grid-template-columns: 1fr 2fr;
  justify-content: space-between;
  z-index: 99;
}
.brand {
  width: 50%;
  font-weight: 600;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  justify-self: end;
  color: white;
}
.navigation {
  /* display: inline-grid;
  grid-template-columns: repeat(4, 1fr); */
  display:flex;
  font-size: .5em;
  width: 50%;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  /* justify-self: end; */
  /* color: white; */
}


/* Landing/Intro Component Styles */
.landing {
  width: 100vw;
  /* max-width: 100%; */
  height: 100vh;
  margin: 0px;
  /* padding-left: 10vw;
  padding-right: 10vw; */
  /* position: relative; */
  /* background-color: black; */
}
.TopLeft {
  height: 120vh;
  width: auto;
  max-width: 150vw;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.BottomRight {
  height: 120vh;
  width: auto;
  max-width: 160vw;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.greeting {
  /* width: 45vw;
  max-width: 100%; */
  font-size: 120px;
  font-size: 15vw;
  line-height: 50px;
  line-height: 15vw;
  color: white;
  position: absolute;
  /* padding-left: 1vw;
  padding-bottom: 1vw; */
  top: 50%;
  left: 50%;
  transform: translate(0%, -110%);
  overflow: visible;
}
.codeNav {
  height: 100vh;
  width: auto;
  max-width: 50vw;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-100%, 0%);
  font-size: 5vw;
}
.name {
  color: #e51b88;
  display:block;
  padding: 0;
  
}
/* About section styles */
.about {
  z-index: 1;
  width: 100vw;
  height: 750px;
  font-weight: 100;
}

